import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import style from '../style.css';
import mu from '../../lib/musher-util';

@observer
class WorkoutStatus extends Component {
    render() {
		const { currentSeason, prevSeason } = this.props;
        const { userStore, workoutPlanStore, teamStore } = this.props.stores;
        const { user } = userStore;
        const { language = 'no' } = user;
		const MONTHS = mu.getMonths(language);

		const { team } = teamStore;

        const { year } = this.props;
		const currentMonth = new Date().getMonth() + 1;
        const currentYear = year || util.getYear();
		const seasonStart = parseInt(currentMonth < 6 ? currentYear - 1 : currentYear, 10);
        const months = util.monthRange(`${seasonStart}-06-01`, `${seasonStart + 1}-05-31`);
        let lastPlanMonth;

        return (
            <div class='container-fluid mb-2'>
                <div class='row mt-2'>
                    <div class='col-12'>
                        <h5>
                            <Text id='workout.totals.title'>Treningsplanstatus</Text>
                        </h5>
                    </div>
                </div>
                <div class='row mt-2 border-bottom pb-2'>
                    {months && months.map((month) => {
                        if (month.year > currentYear || (month.year === currentYear && month.month >= currentMonth)) {
                            return '';
                        }
                        const planMonth = workoutPlanStore.sumWorkoutPlanMonth(month.year, month.month + 1);
                        if (!planMonth) {
                            return '';
                        }
                        lastPlanMonth = planMonth;
                        const summaryMonth = userStore.findWorkoutSummaryMonth(month.year, month.month + 1);
                        const percCompleted = summaryMonth / planMonth * 100;
                        const height = percCompleted > 100 ? 100 : util.format(percCompleted, 0);
                        return (
                            <div class='col-3 text-center'>
                                {/* <div class='w-100 mt-1'>
                                    <small class='text-muted'>
                                        {planMonth}km
                                    </small>
                                    <small class='text-info'>
                                        {summaryMonth}km
                                    </small>
                                </div> */}
                                <div class='px-auto w-100 clearfix'>
                                    <div class={`progress ${style['progress-bar-vertical']}`}
                                        style={' margin-left: calc(50% - 20px);'}
                                    >
                                        <div class='progress-bar progress-bar-striped bg-success' role='progressbar'
                                            style={`height: ${height}%; width: 40px;`}
                                            aria-valuenow={height} aria-valuemin='0' aria-valuemax='100'
                                        >
                                            {util.format(percCompleted, 0)}%
                                        </div>
                                    </div>
                                </div>
                                <div class='w-100 mt-1'>
                                    {MONTHS[month.month]} {month.year}
                                </div>
                            </div>
                        );
                    })}
                    {!lastPlanMonth && <div class='col-12 text-center'>
                        <h3>
                            <Text id='workout.plan.no-workout-plan-found' fields={{ name: team.name }}>Ingen treningsplaner funnet for team {team.name}.</Text>
                        </h3>
                    </div>}
                </div>

            </div>
        );
    }
}

export default WorkoutStatus;
