import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

const initialState = {
    menu: {
		profile: false,
		main: false,
	},
	searchText: '',
};

@observer
class Notifications extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    render() {
        const { userStore } = this.props.stores;
		const { notifications = [] } = userStore;
		const unreadNotificatins = notifications.filter(e => !e.read);
		const unread = unreadNotificatins.length || 0;

        if (unread < 3) {
            return '';
        }

		return (
            <div class='col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mt-4 mb-4 clearfix px-0'>
                <div class='alert alert-danger' role='alert'>
                    <a href='/users/notifications' class='text-dark'>
                        <i class='fas fa-bell mr-2 text-dark' />
                        <span class='badge badge-pill badge-danger'>{unread}</span> <Text id='notifications.unread'>Uleste varslinger. Klikk her for 친 se alle...</Text>
                    </a>
                </div>
			</div>
		);
	}
}

export default Notifications;
