import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

@withText((props) => ({
    week: <Text id='workout.graph.week' />,
    distance: <Text id='workout.graph.distance' />,
    ascend: <Text id='workout.graph.ascend' />,
    plan: <Text id='workout.graph.plan' />,
}))
@observer
class WorkoutTable extends Component {
    render() {
        const { userStore } = this.props.stores;
        const { isLoading, year, month, showHeader = true } = this.props;
        const {
            user,
		} = userStore;
        const { language = 'no' } = user;
		const MONTHS = mu.getMonths(language);

        const currentMonth = month || new Date().getMonth() + 1;
		const currentYear = year || util.getYear();
		const seasonStart = parseInt(currentMonth < 6 ? currentYear - 1 : currentYear, 10);

        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(year, monthNum) || {};
        const seasonMonths = util.monthRange(`${seasonStart}-06-01`, `${seasonStart + 1}-05-31`);
        const prevSeasonMonths = util.monthRange(`${seasonStart - 1}-06-01`, `${seasonStart}-05-31`);
        const seasonWeeks = util.weekRange(`${seasonStart}-06-01`, `${seasonStart + 1}-05-31`);

        const xTicksAll = seasonWeeks.map(wo => wo.week);
        const parts = xTicksAll.length % 6;
        const partSize = Math.floor(xTicksAll.length / parts);
        const xTicks = [];
        for (let i = 0, l = xTicksAll.length; i < l; i += partSize) {
            xTicks.push(xTicksAll[i]);
        }

        return (
            <>
                {showHeader && <h5><Text id='workout.graph.title'>Treningsstatistikk</Text> {seasonStart} - {seasonStart + 1}</h5>}
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <small class='mb-4'><small>
                        <table class='table table-sm font-weigth-lighter border-bottom'>
                            <thead>
                                <tr>
                                    <th style='padding: .15rem;'><Text id='workout.graph.season'>Sesong</Text></th>
                                    {seasonMonths.map(m => (
                                        <th class='text-right' style='padding: .15rem;'><a href={`/workouts/month/${m.month}/${m.year}`}>{MONTHS[m.month - 1]}</a></th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th scope='row' style='padding: .15rem;'>{seasonStart - 1} - {seasonStart}</th>
                                    {prevSeasonMonths.map(m => {
                                        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(m.year, m.month) || {};
                                        return (
                                            <>
                                                <td class='text-right' style='padding: .15rem;'>
                                                    {/* <nobr>{summaryMonth.distance}</nobr><br /> */}
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month)}</nobr><br />
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month, 'elevation')}</nobr><br />
                                                </td>
                                            </>
                                        );
                                    })}
                                    <td class='text-muted' style='padding: .15rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                                <tr>
                                    <th scope='row' style='padding: .15rem;'>{seasonStart} - {seasonStart + 1}</th>
                                    {seasonMonths.map(m => {
                                        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(m.year, m.month) || {};
                                        return (
                                            <>
                                                <td class='text-right' style='padding: .15rem;'>
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month)}</nobr><br />
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month, 'elevation')}</nobr><br />
                                                </td>
                                            </>
                                        );
                                    })}
                                    <td class='text-muted' style='padding: .15rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </small></small>

                </div>
            </>
        );
    }
}

export default WorkoutTable;
